.searchContainer{
    display: flex;
    justify-content: center;
}

.searchBox{
    position: relative;
}

.searchButton{
    position: absolute;
    right: 8px;
    top:0;
    /* cursor: pointer; */
    color: black;
    height: 100%;
    border: none;
    background: none;
}

.searchInput{
    height: 30px;
    font-size: 1.2rem;
    border-radius: 10px;
    padding: 5px 30px 5px 10px;
    border: 1px solid white;

}

.searchInput:focus,
.searchButton:focus{
    outline: none;
    border: 1px solid #E91E62;
    box-shadow: 0 0 5px #E91E62;
}